.profile__name {
    color: rgba(255, 255, 255, 1);
    font-weight: 500;
    font-size: 42px;
    line-height: 48px;
    margin: 0;
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
}

@media screen and (max-width: 767px) {
    .profile__name {
        margin-top: 22.25px;
        text-align: center;
        }
}

@media screen and (max-width: 575px) {
    .profile__name {
        font-size: 27px;
        line-height: 32.68px;
        }
}

